<template>
  <div class="milkfarm" v-if="isLoaded">
    <div class="milkfarm-navbar">
      <div class="game-title">
        <h1>Milkfarm</h1>
      </div>
      <div class="menu">
        <TextButton v-for="tab in tabs" :key="tab" @click="selectedTab=tab.id" :class="{'selected-tab': selectedTab === tab.id}">
          <p>{{ tab.label }}</p>
        </TextButton>
      </div>
    </div>
    <keep-alive>
      <component :is="selectedTab" v-bind="currentProperties"></component>
    </keep-alive>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import Farm from '@/Farm'
import ValueMeter from '@/components/ValueMeter.vue'
import StaffView from '@/components/StaffView.vue'
import ResearchView from '@/components/ResearchView.vue'
import TextButton from '@/components/TextButton.vue'
import globalState from '@/store/globalState'
import { unref } from 'vue'
import Laboratory from '@/Laboratory'
import AdminView from '@/components/AdminView.vue'

@Options({
  components: {
    ValueMeter, StaffView, ResearchView, AdminView, TextButton
  },
  props: {
  },
  methods: {
    log(msg: any) {
      console.log(msg)
    }
  },
  computed: {
    currentProperties: function() {
      if (this.selectedTab === 'StaffView') {
        return { farm: this.farm }
      }
      if (this.selectedTab === 'ResearchView') {
        return { farm: this.farm }
      }
      if (this.selectedTab === 'AdminView') {
        return { farm: this.farm }
      }
    }
  },
  async created() {
    await globalState().load()
    let farm = new Farm()
    this.farm = farm
    this.isLoaded = true
  }
})
export default class Milkfarm extends Vue {
  isLoaded = false
  farm!: Farm
  tabs = [
    { id: 'AdminView', label: 'Admin' },
    { id: 'StaffView', label: 'Staff' },
    { id: 'ResearchView', label: 'Research' }
  ]

  selectedTab: string = 'AdminView'
}
</script>

